import React , {Component} from 'react';
import './Portfolio.css'
import {Row , Col} from 'react-bootstrap'
import '../SkillChart/SkillChart.css';

class Portfolio extends Component{

  projects = [
    {
      title: 'GetSetDine',
      img: '/assets/getsetdine.png'
    },
    {
      title: 'Freshka Foods',
      img: '/assets/freshka.png'
    },
    {
      title: 'Falari Wholesalers',
      img: '/assets/falari.jpg'
    },
    {
      title: 'Stud Muffin',
      img: '/assets/studmuffin.png'
    },
    {
      title: 'Scash 15',
      img: '/assets/scash15.png'
    },
    {
      title: 'Monginis Catalog',
      img: '/assets/monginis-catalog.png'
    }
  ]

  constructor(props){
    super(props);
  }


  render(){
    return (
      <div className = "skills-holder">
        <div style={{position:'absolute' , background: '#00000077' , height:'100%', width:'100%'}}>
        <div style={{marginLeft: '72px'}}>
          <label  style={{ marginTop:'20px' , color:'white'}}className = "details-item-title-text">Portfolio</label>
          <div style={{minHeight: '80px'}}>
            <Row style={{margin:'0px' }}>
              {
                this.projects.map(project=>{
                  return(
                    <Col xs={6} sm={6} lg={4} md={4}>
                      <div className = "project-tile">
                        <img src= {project.img} style={{position:'absolute', top:'0px' , left:'0px' , width:'100%' , height:'100%'}}></img>
                        <div style={{position:'absolute', top:'0px' , left:'0px' , width:'100%' , height:'100%' , background: '#00000044'}}></div>
                          <label className = "project-title-text">
                            {project.title}
                          </label>
                      </div>
                    </Col>
                  )
                })
              }
            </Row>
          </div>
        </div>
        </div>
      </div>
    )
  }
}

export default Portfolio;
