import React , {Component} from 'react';
import {Row , Col} from 'react-bootstrap'
import './SkillChart.css';
import ReactSpeedometer from "react-d3-speedometer"

class SkillChart extends Component{


  constructor(props){
    super(props)
  }

  render(){
    return(
      <div className = "skills-holder">
        <div style={{position:'absolute' , background: '#000000aa' , height:'100%', width:'100%'}}>
          <div style={{marginLeft: '90px'}}>
            <label  style={{color:'white' , marginTop:'20px'}}className = "details-item-title-text">Skills</label>
            <div style={{width: '100%' , height:'500px'}} >
                <Row style={{margin:'0px' , padding:'0px'}}>
                  <Col lg={4} xs={12} >
                  <div className = "skill-tile-holder">
                  <ReactSpeedometer
                    maxValue={100}
                    value={80}
                    needleColor="red"
                    startColor="#2478d5"
                    segments={3}
                    textColor = "#ffffff00"
                    endColor="#2478d5"
                    ringWidth = {20}
                    currentValueText = {"React JS"}
                  />
                  </div>
                  </Col>
                </Row>
            </div>
        </div>
        </div>
      </div>
    )
  }

}

export default SkillChart
